import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Library from '../views/Library.vue'
import HomeAutomation from '../views/HomeAutomationViews/HomeAutomation.vue'
import DevicesControl from '../views/HomeAutomationViews/DevicesControl.vue'
import HomeAutomationConfig from '../views/HomeAutomationViews/HomeAutomationConfig.vue'
import Info from '../views/HomeAutomationViews/Info.vue'
import Login from '../views/Login.vue'

import store from "../store"

Vue.use(VueRouter)

const ifNotAuthenticated = (to, from, next) => {
  var session = Vue.$cookies.get('session')
  var is_auth = store.getters.isAuthenticated(session)
  console.log("Auth: " + String(is_auth))
  if (!is_auth) {
    store.dispatch("auth_logout")
    next();
    return;
  }
  next("/");
}

const ifAuthenticated = (to, from, next) => {
  var session = Vue.$cookies.get('session')
  var is_auth = store.getters.isAuthenticated(session)
  console.log("Auth: " + String(is_auth))
  if (is_auth) {
    store.dispatch("auth_login", session)
    next();
    return;
  }
  store.dispatch("auth_logout")
  next("/login");
}

const optAuthentication = (to, from, next) => {
  var session = Vue.$cookies.get('session')
  var is_auth = store.getters.isAuthenticated(session)
  console.log("Auth: " + String(is_auth))
  if (is_auth) {
    store.dispatch("auth_login", session)
  }
  else {
    store.dispatch("auth_logout")
  }
  next();
}

  const routes = [
  {
    path: '/index',
    name: 'Home',
    component: Home,
    beforeEnter: optAuthentication
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: About,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/library',
    name: 'Library',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: Library,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/homeautomation',
    name: 'HomeAutomation',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: HomeAutomation,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/homeautomation/devicescontrol',
    name: 'DevicesControl',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: DevicesControl,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/homeautomation/info',
    name: 'Info',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: Info,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/homeautomation/config',
    name: 'HomeAutomationConfig',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: HomeAutomationConfig,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/login',
    name: 'Login',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    component: Login,
    beforeEnter: ifNotAuthenticated
  }
  
  
]

const router = new VueRouter({
  routes
})
router.replace({ path: '/index', redirect: '/' })
export default router
