import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: 'xalala',
    status: '',
    logged_user: ''
  },
  mutations: {
    auth_request (state) {
      state.status = 'loading'
    },
    auth_success (state, payload) {
      state.status = 'success',
      state.token = payload.token,
      state.logged_user = payload.user
    },
    auth_error (state) {
      state.status = 'error'
    },
    auth_logout (state) {
      state.status = '',
      state.token = null,
      state.logged_user = ''
    }
  },
  actions: {
    auth_request ({commit, dispatch}, user) {
      commit('auth_request')

      fetch('http://127.0.0.1/betterlifeapis/users/login', {
        method: 'post',
        headers: new Headers({
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        }),
        body: JSON.stringify({
          "username": user.username,
          "password": user.password
          })
      }).then(function (response) {
        if (response.status === 200) { // && 'token' in response.body
          console.log("Requesting logging in")
          return response.json()       
        }
        else {
          console.log("Error on log in")
          commit("auth_error")
          throw Error("Throwing exception on log in")
        }
      }).then((jsonData) => {
        console.log(jsonData)
        dispatch("auth_login", {'token': jsonData["token"], 'user':jsonData["username"]})
      }).catch(function(error) {
        console.log("Catching error")
        console.log(error)
      })
    },
    auth_logout ({commit}) {
      console.log("Logging out")
      commit("auth_logout")
      Vue.$cookies.remove('session')
     },
     auth_login ({commit}, payload) {
      var token = payload.token
      var username = payload.user
      // console.log("Logging in")
      // console.log(username)
      // console.log(token)
      commit("auth_success", {'user': username, 'token': token})
      Vue.$cookies.set('session', {'user': username, 'token': token}, 60)
        
     }
  },
  modules: {
  },
  getters: {
    //isAuthenticated: state => test_token => state.logged_user === test_token,
    isAuthenticated (state)  {
      return function (session) {
        console.log("checking auth")
        // console.log(session)
        // console.log(state.logged_user)
        // console.log(state.token)
        if (session) {
          return (state.token === session.token)
        }
        return false
        
      }
    },
    authStatus: state => state.status,
    loggedUser: state => state.logged_user
  }
})
