<template>
  <div id="app">
    <div id="nav">
      <router-link :to="{ name: 'Home', params: {}}">Home</router-link> |
      <router-link :to="{ name: 'About', params: {}}">About</router-link> |
      <template v-if="!is_auth"> <router-link :to="{ name: 'Login', params: {}}" class="btn btn-outline-success">Login</router-link> |
      </template>
      <template v-if="is_auth">
        <button @click="logout" class="btn btn-outline-danger">Logout</button>
      </template>
    </div>
    <template v-if="get_logged_user"> Bem vindo {{ get_logged_user }} </template>
    <hr />
    <router-view/>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Vue from 'vue'

export default {
  data() {
    return {

    }
  },
  computed: {
    ...mapGetters([
      'isAuthenticated',
      'loggedUser'
    ]),
    is_auth() {
      return this.isAuthenticated(Vue.$cookies.get('session'))
    },
    get_logged_user() {
      return this.loggedUser
    }
  },
  
  methods: {
    ...mapActions([
      'auth_logout'
    ]),
    logout: function () {
      this.$store.dispatch("auth_logout", {})
      this.$router.push("/login")
    },
  }
}
</script>

