<template>
  <div class="container">
    <h3>Devices Control</h3>
    
    <div class="container">
      <b-row class="text-center">
        <b-col cols="8">
          Captura automática a cada {{ timer/1000 }} segundos
          <b-form-input 
            v-model="timer" 
            type="range" 
            :min="200" 
            :max="60000" 
            :step="100"
            @change="reset_getting_data()"
          >
          </b-form-input>
        </b-col>

        <b-col cols="1">
          <b-button @click="stop_getting_data()" v-if="this.myinterval" variant="danger">Stop</b-button>
          <b-button @click="start_getting_data()" v-if="!this.myinterval" variant="success">Start</b-button>
        </b-col>
      </b-row>
    </div>

    <ul>
      <li v-for="(device, ind) in devices" :key="ind">
        <div class="container device">
          <b-card :title="device.home_name" :sub-title="device.room_name">

            <b-container class="bv-example-row">
              <div class="device-data">
                <b-row class="text-left">
                  <b-col cols="8">
                    <b-card-text class="device-name">
                      {{ device.device_name }} 
                      <button @click="toggle_show_properties(ind)" class="btn btn-outline-warning properties-button">
                        <b-container class="bv-example-row">
                          <b-row class="text-center ">
                              &#183;&#183;&#183;                            
                          </b-row>
                          
                        </b-container>

                      </button>
                    </b-card-text>
                  </b-col>
                  <b-col>
                    <b-card-text class="device-value">
                      <div>
                        <!--label for="range-2"></label-->
                        <b-container class="bv-example-row">
                          <b-row class="text-left">
                            <b-col cols=8>
                              <b-form-input 
                                v-model="device.data.current_value" 
                                type="range" 
                                :min="device.data.value_min" 
                                :max="device.data.value_max" 
                                :step="device.data.step"
                                @change="update_device_value(device)"
                              >
                              </b-form-input>
                            </b-col>
                            <b-col>
                              <div class="">{{ device.data.current_value }}</div>
                            </b-col>
                          </b-row>
                          
                        </b-container>                
                      </div>
                      
                    </b-card-text>
                  </b-col>
                </b-row>
              </div>

              <div v-if="show_properties[ind]" class="device-properties">
                <b-row class="device-properties-header">
                  <b-col>
                    Device Type:
                  </b-col>
                  <b-col>
                    Value Type:
                  </b-col>
                  <b-col>
                    Min Value:
                  </b-col>
                  <b-col>
                    Max Value:
                  </b-col>
                  <b-col>
                    Step Size:
                  </b-col>
                </b-row>

                <b-row>
                  <b-col>
                    {{ device.data.device_type }}
                  </b-col>
                  <b-col>
                    {{ device.data.value_type }}
                  </b-col>
                  <b-col>
                    {{ device.data.value_min }}
                  </b-col>
                  <b-col>
                    {{ device.data.value_max }}
                  </b-col>
                  <b-col>
                    {{ device.data.step }}
                  </b-col>
                </b-row>
              </div>
  
            </b-container>
          </b-card>
        </div>

      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeAutoDevices',
  props: {
    
  },
  data() {
    return {
      devices: [],
      //device: {'data':{}},
      show_properties: [false],
      myinterval: null,
      timer: 1000
    }
  },
  mounted: function() {
    this.get_data()
     
    //this.start_getting_data(1000)
  },
  methods: {
    toggle_show_properties (ind) {
      var new_value = this.show_properties[ind]?false:true
      //var new_value = dev['show_properties']?false:true
      //console.log(new_value)
      this.$set(this.show_properties, ind, new_value)  
    },
    update_device_value (device) {
      console.log("Updated " + String(device.data.current_value))
      //home_name = device.home_name

      fetch('http://127.0.0.1/betterlifeapis/devices/?home_name=' + 
        device.home_name + '&room_name=' + 
        device.room_name + '&device_name=' + 
        device.device_name + '&new_value=' + 
        String(device.data.current_value), {
        method: 'put',
        //parameters: JSON.stringify({'home_name': 'Ibiza', 'room_name': 'Quarto', 'device_name': 'Ventilador', 'new_value': String(device.data.current_value)})
      }
      )
      .then((response) => {
        console.log(response)
        return response.json()
      })
      // .then((jsonData) => {
      //   this.devices = jsonData.all_devices
      //   //this.device = this.devices[0]
      //   //this.device['show_properties'] = false
      //   this.show_properties[0] = false
      // })

    },
    get_data () {
      fetch('http://127.0.0.1/betterlifeapis/devices/get_all', {
        method: 'get'
        })    
        .then((response) => {
          console.log(response)
          return response.json()
        })
        .then((jsonData) => {
          this.devices = jsonData.all_devices
          //this.device = this.devices[0]
          //this.device['show_properties'] = false
          this.show_properties[0] = false
        })
    },
    start_getting_data() {
      this.myinterval = window.setInterval(() => {
       this.get_data()
      }, this.timer)
    },
    stop_getting_data(){
      window.clearInterval(this.myinterval)
      console.log("blob")
      this.myinterval = false
      console.log(this.myinterval)
    },
    reset_getting_data() {
      if (this.myinterval){
        this.stop_getting_data()
        this.start_getting_data()
      }
    }
  }
}
</script>

