<template>
  <div class="container">
    <h3>Configuration</h3>
    span: mensagem do servidor<br>
    "atualizar server e port via api (criar rota)"<br>
    <label for="server_host">MQTT Host: </label>
    <b-form-input 
      id="server_host" 
      v-model="server_host" 
      type="text"
      :disabled="!edit_enabled"
    > </b-form-input><br>

    <label for="server_port">MQTT Host: </label>
    <b-form-input 
      id="server_port" 
      v-model="server_port" 
      type="text"
      :disabled="!edit_enabled"
    > </b-form-input><br>

    <button @click="update_mqtt_server()" class="btn btn-outline-warning" :disabled="!edit_enabled">
      Atualizar Server
    </button>

    <button @click="refresh_mqtt_server()" class="btn btn-outline-warning">
      Checar Server
    </button>
    <br>
    <span> Status do Server: {{ server_status }} </span>
    
    "atualizar status servidor via api (criar rota)"
    
  </div>
</template>

<script>
export default {
  name: 'HomeAutoConfig',
  props: {
    
  },
  data() {
    return {
      server_host: 0,
      server_port: 0,
      server_status: 0,
      edit_enabled: true
    }
  },
  mounted: function() {
    this.refresh_mqtt_server()
  },
  beforeCreate: function() {
    if (!this.$session.exists()) {
      console.log("Authentication needed")
      this.$router.push("/login")
    }
  },
  methods: {
    toggle_edit_enabled () {
      this.edit_enabled = !this.edit_enabled
    },
    refresh_mqtt_server () {
      console.log("Refreshing MQTT Server")
      
      fetch('http://127.0.0.1/betterlifeapis/mqttcfg',
      {
        method: 'get'
      }
      )
      .then((response) => {
        console.log(response.status)
        if (response.status == 200) {
          this.server_status = 1
        }
        else {
          this.server_status = 0
        }
        return response.json()
      })
      .then((jsonData) => {
          console.log(jsonData.status)
          if (jsonData.status == "MQTT reconnected") {
            this.server_host = jsonData.hostname
            this.server_port = jsonData.port_number
            this.server_status = 1
          }
          else {
            this.server_status = 0
          }
      })
    },
    update_mqtt_server () {
      console.log("Updating MQTT Server")
      
      fetch('http://127.0.0.1/betterlifeapis/mqttcfg', 
      {
        method: 'post',
        headers: new Headers({
         'Content-Type': 'application/json',
         'Accept': 'application/json',
        }),
        body: JSON.stringify({
                                "hostname": this.server_host,
                                "port_number": this.server_port
                              })
      }
      )
      .then((response) => {
        console.log(response)
        if (response.status == 200) {
          this.server_status = 1
        }
        else {
          this.server_status = 0
        }
        
        return response.json()
      })
      .then((jsonData) => {
        console.log(jsonData)
        if (jsonData.status == "MQTT Server was reset") {
            this.server_status = 1
          }
          else {
            this.server_status = 0
          }
        //this.server_status = 1
      })
    },
  }
}
</script>

